<template>
  <div class="inner" style="margin-top:20px;">
    <div class="appCon">
      <div class="appView clearfix">
        <div class="appLogo">
          <img src="@/assets/img/erzhan.jpg" alt="">
        </div>
        <div class="appIntro">
          <h2>title</h2>
          <el-rate
            v-model="rateVal"
            disabled
            text-color="#ff9900"
            score-template="{value}">
          </el-rate>
          <div class="downlo">
            <button>安全下载</button>
          </div>
        </div>
        <div class="qr">
          <img src="@/assets/img/qr2.png" alt="">
          <p>扫一扫下载</p>
        </div>
      </div>
      <div class="padbot20">
        <div class="review">
          <h2>
            <i class="el-icon-ali-bubblestalk"/>小编点评
          </h2>
          <p>按时到四点</p>
        </div>
      </div>
    </div>
    <div class="intro">
      <div class="detail-menu">
        <ol>
          <li class="current">
            <span>简介</span>
          </li>
        </ol>
      </div>
      <div>图片</div>
      <div>介绍</div>
      <div>图片</div>
      <div>介绍</div>
    </div>
  </div>
</template>
<script>
export default {
  data: function () {
    return {
      id: 0,
      rateVal: 4
    }
  },
  mounted () {
    // this.id = this.$router.params.id
    console.log(this.$route.query.id)
  }
}
</script>
<style lang="scss" scoped>
.appCon{
  margin: 15px;
}
.appView{
  margin: 15px;
  .appLogo{
    float: left;
    img{
      height: 160px;;
    }
  }
  .appIntro{
    margin: 10px 10px 10px 20px;
    text-align: left;
    float: left;
    .downlo{
      margin-top: 20px;
      button{
        width: 98px;
        height: 36px;
        background: #24aa42;
        color: white;
        border-radius: 4px;
        border: none;
      }
    }
    h2{
      font-size: 24px;
      margin-top: 10px;
    }
  }
  .qr{
    float: right;
    img{
      height: 90px;;
    }
  }
}
.padbot20{
  border-bottom: 1px solid #f4f4f4;
}
.review{
  height: 100%;
  text-align: left;
  background: #f7f7f7;
  padding: 20px;
  h2{
    font-size: 14px;
    margin-bottom: 15px;
  }
  i{
    font-size: 14px;
  }
  p{
    font-size: 12px
  }
}

@media screen and (max-width: 440px) {
  .appView{
    .qr{display: none}
    .appLogo{
      img{
        height: 100px;
      }
    }
    .appIntro{
      .downlo{
        margin-top: 10px;
        button{width:76px;height: 24px;font-size: 12px; }
      }
      h2{
        margin: 0;
      }
    }
  }
}
.intro{
  .detail-menu {
    // margin: 10px 10px 5px 10px;
    height: 41px;
    line-height: 40px;
    overflow: hidden;
    ol {
      border-bottom: 1px solid #009900;
      li{
        color: #009900;
      }
    }
  }
}
</style>



